<template>
  <v-card outlined>
    <v-card-text>
      <v-row>
        <v-col cols="3">
          <v-card outlined color="#fff">
            <v-card-title>Datos Generales Entidad</v-card-title>
            <v-card-text>
              <ValidationObserver ref="formDatosEntidad" v-slot="{ validate, reset }">
                <v-form @reset.prevent="reset">
                  <v-row>
                    <v-col cols="12">
                      <ValidationProvider v-slot="{ errors }" name="Nombre" rules="required">
                        <v-text-field
                          v-model="entidad.nombre"
                          label="Nombre"
                          dense
                          :error-messages="errors"
                        ></v-text-field>
                      </ValidationProvider>
                    </v-col>
                    <v-col cols="12">
                      <ValidationProvider
                        v-slot="{ errors }"
                        name="Representante legal"
                        rules="required"
                      >
                        <v-text-field
                          v-model="entidad.representanteLegal"
                          label="Representante legal"
                          dense
                          :error-messages="errors"
                        ></v-text-field>
                      </ValidationProvider>
                    </v-col>
                    <v-col cols="12">
                      <ValidationProvider v-slot="{ errors }" name="Tipologia" rules="required">
                        <v-select
                          v-model="entidad.tipologia"
                          :items="tipologias"
                          label="Tipologia entidad"
                          dense
                          multiple
                          chips
                          item-text="nombre"
                          item-value="id"
                          return-object
                          :error-messages="errors"
                        ></v-select>
                      </ValidationProvider>
                    </v-col>
                  </v-row>
                </v-form>
              </ValidationObserver>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="9">
          <lista-paginacion :datos="entidad.sedes" :config="configLista" v-on:editarItem="editarEntidad">
            <template slot="contenido" slot-scope="propiedades">
              <div v-if="propiedades.item.encabezado.index==0">
                <v-list-item-content>
                  <h4 style="font-weight: 400;">{{propiedades.item.data.direccion}}</h4>
                  <v-list-item-subtitle>{{propiedades.item.data.municipio.MunicipioLbl}}, {{propiedades.item.data.departamento.DepartamentoLbl}}</v-list-item-subtitle>
                  <v-list-item-subtitle>Longitud: {{propiedades.item.data.coordenadas.longitud}}</v-list-item-subtitle>
                  <v-list-item-subtitle>Latitud: {{propiedades.item.data.coordenadas.latitud}}</v-list-item-subtitle>
                  <v-list-item-subtitle>Contactos: {{propiedades.item.data.contactos.length}}</v-list-item-subtitle>
                </v-list-item-content>
              </div>
              <div v-else-if="propiedades.item.encabezado.index==1">
                <v-list dense style="padding: 0px;">
                  <v-list-item
                    v-for="(telefono,index) in propiedades.item.data.telefonos"
                    :key="`${index}-${telefono.valor}`"
                  >
                    <v-list-item-content>
                      <v-list-item-title>
                        <v-icon color="indigo">mdi-phone</v-icon>
                        {{telefono.valor}}
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
              </div>
              <div v-else-if="propiedades.item.encabezado.index==2">
                <v-list dense style="padding: 0px;">
                  <v-list-item
                    v-for="(fax,index) in propiedades.item.data.faxes"
                    :key="`${index}-${fax.valor}`"
                  >
                    <v-list-item-content>
                      <v-list-item-title>
                        <v-icon color="indigo">local_printshop</v-icon>
                        {{fax.valor}}
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
              </div>
              <div v-if="propiedades.item.encabezado.index==3">
                <v-list dense style="padding: 0px;">
                  <v-list-item
                    v-for="(correo,index) in propiedades.item.data.correos"
                    :key="`${index}-${correo.valor}`"
                  >
                    <v-list-item-content>
                      <v-list-item-title>
                        <v-icon color="indigo">mdi-email</v-icon>
                        {{correo.valor}}
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
              </div>
            </template>
          </lista-paginacion>
        </v-col>
        <v-col cols="12">
          <div class="text-center">
            <v-btn class="ma-2" color="primary" @click="guardarEntidad">
              <v-icon left>check</v-icon>Guardar entidad
            </v-btn>
            <v-btn class="ma-2" color="default" @click="limpiar">
              <v-icon left>clear</v-icon>Cancelar
            </v-btn>
          </div>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import { store } from "../servicios/store";
import ListaPaginacion from "../components/ListaPaginacion";

export default {
  components: {
    ListaPaginacion
  },
  mounted(){
    this.storeState=store.state;
    this.entidad=store.state.entidad;
  },
  data() {
    return {
      storeState:null,
      entidad: {
        nombre: null,
        representanteLegal: null,
        tipologia: [],
        sedes: []
      },
      tipologias: [
        { id: 1, nombre: "Participacion" },
        { id: 2, nombre: "Promocion" },
        { id: 3, nombre: "Difusion" },
        { id: 4, nombre: "Sensilibizacion" }
      ],
      configLista: {
        titulo: "Sedes entidad",
        color: "#fff",
        encabezados: [
          { texto: "Datos sede", ancho: 3 },
          { texto: "Telefono(s)", ancho: 2 },
          { texto: "Fax(es)", ancho: 2 },
          { texto: "Correo(s)", ancho: 5 }
        ],
        itemAvatar: {
          ver: true,
          icono: "place"
        }
      }
    };
  },
  methods: {
    guardarEntidad() {
      //store.setEjecutarValidacion(true);
      this.$refs.formDatosEntidad.validate().then(esValido => {
        if (esValido) {
          console.log("guardar");
        }
      });
    },
    editarEntidad(entidad){
      
       console.log("Entidad desde datosEntidad: "+JSON.stringify(entidad)); 
    },
    limpiar() {
      this.$refs.formDatosEntidad.reset();
    }
  },
};
</script>


